.contact {
  height: 100vh;
  width: 100vw;
  background-color: black;
  display: flex;
  flex-direction: column;

  .title {
    min-height: 20vh;
    background-color: #251170;
    min-width: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .container {
    min-height: 80vh;
    min-width: 300px;
    height: 100%;
    width: 100%;
    box-sizing: border-box;
    flex-grow: 1;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;

    .wrapperContactFormsContainer {
      height: 80vh;
      width: 45%;

      .contactFormsContainer {
        height: 100%;
        width: 100%;
        min-width: 360px;
        min-height: 360px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        box-sizing: border-box;
        padding-left: 7%;
        padding-right: 7%;
        animation-duration: .8s;
        animation-fill-mode: both;
        animation-name: fadeInUp;

        .titleSecondOrder {
          box-sizing: border-box;

          & h4 {
            color: #fff;
            font-family: Poppins, sans-serif;
            font-size: 22px;
            font-weight: 500;
            position: relative;
            left: 110px;
            flex-basis: inherit;

            &::after {
              content: "";
              height: 3px;
              width: 90px;
              display: block;
              background-color: #eb83f8;
              position: absolute;
              left: -108px;
              top: 15px;
            }
          }
        }

        .form {
          display: flex;
          flex-direction: column;

          & input {
            margin-bottom: 5px;
          }
        }

        .sendContactForm {
          width: 150px;
          margin: 0 auto;
        }
      }
    }

    .wrapperMap {
      width: 55%;
      height: 80vh;

      .map {
        width: 100%;
        height: 100%;
        min-height: 500px;
        min-width: 360px;
        border: none;
        animation-duration: 1s;
        animation-delay: 1.8s;
        animation-fill-mode: both;
        animation-name: isVisible;
      }
    }
  }
}


@media (min-width: 1200px) {
  .contact {
    flex-wrap: nowrap;
  }
  .title {
    min-width: 300px;
  }
}
@media (min-width: 992px) and (max-width: 1199px) {
  .title {
    width: 100%;
    writing-mode: horizontal-tb;
    transform: none;
  }
}
@media (min-width: 768px) and (max-width: 991px) {
  .title {
    width: 100%;
    writing-mode: horizontal-tb;
    transform: none;
  }
  .contactFormsContainer {
    width: 100%;
    height: 360px;
  }
  .map {
    width: 100%;
    height: 500px;
  }
}
@media (min-width: 576px) and (max-width: 767px) {
  .title {
    width: 100%;
    min-height: 150px;
    writing-mode: horizontal-tb;
    transform: none;
  }
  .container {
    height: 100%;
  }
  .contactFormsContainer {
    width: 100%;
    height: 360px;
  }
  .map {
    width: 100%;
    height: 500px;
  }
}
@media (max-width: 575px) {
  .contact {
    height: 200vh;
    background-color: #251170;

    .title {
      width: 100%;
      min-height: 150px;
      transform: none;
      padding-left: 50px;
    }

    .container {
      height: 100%;
      width: 100%;
      display: flex;
      flex-direction: column;
      flex-wrap: nowrap;
      align-items: flex-start;
      justify-content: space-between;

      .wrapperContactFormsContainer {
        height: 70vh;
        width: 100%;

        .contactFormsContainer {
          min-width: 300px;
          min-height: 360px;
          padding-left: 3%;
          padding-right: 3%;
        }
      }

      .wrapperMap {
        width: 100%;
        height: 50%;

        .map {
          animation-duration: .8s;
          animation-delay: 1.2s;
        }
      }
    }
  }
}
@media (max-width: 300px) {
  .contact {

    .container {

      .wrapperMap {
        width: 280px;
        height: 50%;
      }
    }
  }
}
@media (orientation: landscape) and (max-width: 991px) {
  .contact {
    height: 250vh;
    background-color: #251170;

    .title {
      width: 100%;
      min-height: 150px;
      transform: none;
      padding-left: 50px;
    }

    .container {
      height: 100%;
      width: 100%;
      display: flex;
      flex-direction: column;
      flex-wrap: nowrap;
      align-items: flex-start;
      justify-content: space-between;

      .wrapperContactFormsContainer {
        height: 170vh;
        width: 100%;


        .contactFormsContainer {
          min-width: 300px;
          min-height: 360px;
          padding-left: 3%;
          padding-right: 3%;
          justify-content: normal;
        }
      }

      .wrapperMap {
        width: 100%;

        .map {
          animation-duration: .8s;
          animation-delay: 1.2s;
        }
      }
    }
  }
}
@media (orientation: landscape) and (max-width: 991px) and (max-height: 320px) {
  .contact {
    height: 320vh;

    .container {

      .wrapperContactFormsContainer {
        height: 170vh;
      }

      .wrapperMap {
        height: 280px;
      }
    }
  }
}


@keyframes isVisible {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }
  100% {
    opacity: 1;
    transform: translateZ(0);
  }
}